import React from "react";
import Header from "./commonComponent/header";
import SideBar from "./commonComponent/sideBar";
import { Link, useLocation, useParams } from "react-router-dom";
import { useQuery } from "@tanstack/react-query";
import { ratingDetails } from "../apiServices/home/homeHttpService";
import moment from "moment";

function RatingReviewDetails() {
  const location = useLocation();
  const queryParams = new URLSearchParams(location.search);
  let { id } = useParams();

  const { data: details } = useQuery({
    queryKey: ["ratingDetail", id],
    queryFn: async () => {
      return ratingDetails(id);
    },
    onError: (error) => {
      console.log(error);
    },
    select: (data) => data.results.rating,
  });

  return (
    <>
      <Header />
      <div className="admin_main">
        <SideBar />

        <div className="admin_contentpart">
          <div>
            <Link
              to={`/admin/rating-and-review-management?page=${
                queryParams.get("page") || 1
              }`}
              className="comman_btn w-fit"
            >
              <i className="fa fa-arrow-left" />
            </Link>
          </div>
          <div className="row comman_design mx-0 mt-4">
            <div className="col-12">
              <div className="row comman_table pt-md-4 pb-md-4 pb-3 px-md-4 px-2">
                <div className="col-12">
                  <div className="row">
                    <div className="col-12">
                      <div className="row">
                        <div className="col-12">
                          <div className="d-flex flex-wrap gap-3">
                            <div className="mt-4 mt-md-0 justify-content-center d-flex justify-content-md-start w-100">
                              <div className="rating-user-img-wrapper">
                                <img
                                  src={
                                    details?.userDetails?.profile ||
                                    "../../assets/img/user1.png"
                                  }
                                  alt=""
                                  className="w-100 h-100"
                                />
                              </div>
                            </div>
                            <div>
                              <p className="fs-5 fw-semibold mb-0">
                                {details?.userDetails?.name}
                              </p>

                              <div className=" mt-3">
                                <small className="fw-light">
                                  {moment(details?.createdAt).format(
                                    "DD MMM YYYY, hh:mm A"
                                  )}
                                </small>
                              </div>
                              <div className="d-flex gap-3 flex-wrap">
                                <div className="leading-4 fw-light mb-0 w-75">
                                  {details?.description}
                                </div>
                                <div>
                                  <h4 className="fs-3 fw-bold text-center">
                                    {details?.rating}
                                  </h4>
                                  <p className="d-flex gap-2 mt-2">
                                    {[...Array(5)].map((_, index) => (
                                      <i
                                        key={index}
                                        className={`fa fa-star ${
                                          index < details?.rating
                                            ? "main-color"
                                            : ""
                                        } leading-5`}
                                      />
                                    ))}
                                  </p>
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                        <div className="col-12 mt-4">
                          <div className="d-flex flex-wrap gap-4">
                            <div className="col-md-2 col-12">
                              <div className="rating-img-wrapper">
                                <img
                                  src={
                                    details?.image[0] ||
                                    "../../assets/img/pizza1.png"
                                  }
                                  alt=""
                                  className="w-100 h-100"
                                />
                              </div>
                            </div>
                            <div className="col-md-9 col-12">
                              <h4 className="fs-5 fw-semibold mb-0">
                                {details?.restaurantDetails?.name}
                              </h4>
                              <p className="fw-light leading-1">
                                {details?.restaurantDetails?.dish}
                              </p>
                              <div>
                                <p className="leading-4 fw-light mt-4">
                                  {details?.restaurantDetails?.about}
                                </p>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default RatingReviewDetails;
